nav{
    padding: 1rem;
    align-items: center;
    min-height: 10vh;
    display: flex;
    justify-content: space-around;
    h1{
        font-size: 3rem;
        font-family: 'pacifico';
    }
    button {
        background: transparent;
        height: 5rem;
        width: 10rem;
        padding: 0.5rem 1rem;
        border: none;
        cursor: pointer;
        border: 2px solid rgb(65, 65, 65);
        border-radius: 1rem;
        outline-style: none;
        display: flex;
        justify-content: space-around;
        align-items: center;
        transition: all 0.3s ease;
        &:hover{
            color: white;
            background: rgb(65, 65, 65);
        }
    }

}