.library{
    position: fixed;
    top: 0;
    left: 0;
    width: 20rem;
    height: 100%;
    background: white;
    box-shadow: 2px 2px 50px rgb(202, 201, 201);
    overflow: scroll;
    transform: translateX(-100%);
    transition: all 0.5s ease;
    opacity: 0;
    .h1tag{
        color: rgb(0, 0, 0);
        padding : 1rem 2rem 1rem 2rem;
    }
    h1,h3 {
        color: rgb(143, 143, 143);
    }
}

.library-song{
    display: flex;
    align-items: center;
    flex-direction: row;
    padding : 1rem 2rem 1rem 2rem;
    
    
    img{
        border-radius: 0;
        width: 50%;
    }
    cursor: pointer;
    &:hover{
        background-color: rgb(245, 245, 245);
    }
    

}

.song-description{
   
    padding-left: 1rem;
    h1{
        font-size: 1.2rem;
    }
    h3{
        font-size: 0.8rem;
    }
}


*{
    scrollbar-width: thin;
    scrollbar-color: rgb(143, 140, 140);
}

*::-webkit-scrollbar{
    width: 5px;
}

*::-webkit-scrollbar-track{
    background-color: transparent;
}

*::-webkit-scrollbar-thumb{
    background-color: rgb(148, 148, 148);
    border-radius: 20px;
    border: transparent;
}

.selected{
    // background: rgb(255, 191, 95);
    background: #9ab3f5;
    h1,h3 {
        color: rgb(70, 70, 70);
    }
}

.active-library {
    transform: translateX(0%);
    opacity: 100%;
}
