@import url('https://fonts.googleapis.com/css2?family=Ubuntu&display=swap');

.player-container{
    min-width: 20%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;
    h1,h2,h3,p{
        font-family: 'Ubuntu';
        color: rgb(14, 14, 14);
    }
}


.time-controller {
    width: 50%;
    display: flex;
    justify-content: space-around;
    input{
        width: 100%;
       
    }

    p{
        padding: 1rem;
    }
}

.play-control{
    width: 25%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    svg{
        cursor: pointer;
    }
}